@import "https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css";

*{
    box-sizing:border-box;
}

input{
    all:unset;
    box-sizing:border-box;
    appearance:none;
}

body{
    background-color:#c8c8c8;
    font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    font-size:14px;
    color:white;
}

button{
    background-color:white;
    color:black;
}

a{
    text-decoration:none;
    color:inherit;
}

form{
    width:100%;
}

/*이부분 class 가 없음*/
.nweet-input::placeholder{
    color:white;
    opacity:0.9;
}

.container{
    width:100%;
    max-width: 320px;
    display:flex;
    flex-direction: column;
}

.formInput{
    width:100%;
    padding:10px 20px;
    border-radius:20px;
    border:1px solid black;
    text-align:center;
    background-color:white;
    color:black;
}

.formBtn{
    cursor:pointer;
    width:100%;
    padding:7px 20px;
    text-align:center;
    color:white;
    border-radius:20px;
    background-color: #04aaff;
}

.cancelBtn{
    cursor:pointer;
    background-color:tomato;
    border:none;
}

/*profile page*/

.profileForm{
    border-bottom:1px solid rgba(255,255,255,0.9);
    padding-bottom:30px;
    width:100%;
    display:flex;
    flex-direction: column;
}

.logOut{
    margin-top:50px;
}

/*nwitter factory*/
.factoryForm{
    display:flex;
    flex-direction:column;
    align-items:center;
    width:100%;
}

.factoryInput__container{
    display:flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap:wrap;
    position:relative;
    margin-bottom:20px;
    width:100%;
}

.factoryInput__input{
    flex-grow:1;
    height:40px;
    padding:0px 20px;
    color:white;
    border:1px solid #04aaff;
    border-radius:20px;
    font-weight:500;
    font-size:12px;
}

.factoryInput_arrow{
    position: absolute;
    right:0;
    background-color: #04aaff;
    height:40px;
    width:40px;
    padding:10px 0px;
    text-align:center;
    border-radius: 20px;
    color:white;
}

.factoryInput__label{
    color:#04aaff;
    cursor:pointer;
}

.factoryInput__label span{
    margin-right: right 10px;
    font-size:12px;
}

.factoryForm__attachment{
    display:flex;
    flex-direction: column;
    justify-content: center;
}

.factoryForm__attachment img{
    height:80px;
    width:80px;
    border-radius: 40px;
}

.factoryForm__clear{
    color:#04aaff;
    cursor:pointer;
    text-align:center;
}

.factoryForm__clear span{
    margin-right:5px;
    font-size:12px;
}

/*Nweet*/

.nweetEdit .formBtn{
    cursor:pointer;
    margin-top:15px;
    margin-bottom:5px;
}

.nweet{
    margin-bottom:20px;
    background-color:white;
    color:rgba(0,0,0,0.8);
    width:100%;
    max-width: 320px;
    padding:20px;
    border-radius:10px;
    position:relative;
    display:flex;
    flex-direction: column;
}

.nweet h4{
    font-size:14px;
}

.nweet img{
    right:-10px;
    top:20px;
    position:absolute;
    border-radius:50%;
    width:50px;
    height:50px;
    margin-top:10px;
}

.nweet__actions{
    position:absolute;
    right:10px;
    top:10px;
}

.nweet__actions span{
    cursor:pointer;
}

.nweet__actions span:first-child{
    margin-right:10px;
}

/*auth*/

.authContainer{
    display:flex;
    flex-direction: column;
    height:100vh;
    justify-content: center;
    align-items: center;
}

.authBtns{
    display:flex;
    justify-content: space-between;
    width:100%;
    max-width:320px;
}

.authBtn{
    cursor:pointer;
    border-radius:20px;
    border:none;
    padding:10px 0px;
    font-size:12px;
    text-align: center;
    width:150px;
    background:white;
}


/*authForm*/

.authInput{
    max-width:320px;
    width:100%;
    padding:10px;
    border-radius:30px;
    background-color:rgba(255,255,255,1);
    margin-bottom:10px;
    font-size:12px;
    color:black;
}

.authSubmit{
    text-align: center;
    background-color:#04aaff;
    color:white;
    margin-top:10px;
    cursor:pointer;
}

.authError{
    color:tomato;
    text-align:center;
    font-weight:500;
    font-size:12px;
}

.authSwitch{
    color:#04aaff;
    cursor:pointer;
    margin-top:10px;
    margin-bottom:50px;
    display:block;
    font-size:12px;
    text-decoration:underline;
}

